.headerWrapper {
    @apply bg-gradient-to-br relative md:h-[100vh] xl:h-[auto] pb-40 w-full px-4;
}

.headerContentWrapper {
    @apply flex items-center flex-wrap justify-center;
}

.headerTeamWrapper {
    @apply flex flex-col justify-evenly items-center;
    /* @apply flex lg:flex-row; */
}

.headerContent {
    @apply  w-60 ;
}

.headerLeftText {
    @apply relative uppercase font-bold w-full text-center;
}

.headerRightContent {
    @apply relative mx-auto justify-center xl:w-45;
}


.headerRightRecContent {
    @apply -rotate-45 flex flex-col;
}

.image {
    background-image: url('../../resources/images/person.png');
    clip-path: url(#myClip);
    /* height: 300px; */
    /* width: 100%; */
}


/* Bottom Curve */
.headerBottomCurve {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

 /* className={`${styles.headerWrapper} from-steel-blue to-violet font-Inter`}  */

.headerWrapperStyle{
  background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(117, 19, 93, 0.73)), var(--img);
}

#show_bg{
    background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(117, 19, 93, 0.73)), url('../../resources/images/header_background.jpg');
    width: 80%;
    height: 400px;
    background-size: cover;
    color: white;
    padding: 20px;
}
