.footerWrapper {
    @apply absolute left-0 right-0 max-w-full;
    @apply h-auto
    /* @apply absolute lg:top-1/2; */
}

.footerLinksWrapper {
    @apply flex flex-col justify-evenly;
    /* @apply flex lg:flex-row; */
}

.footerLinks {
    @apply text-lg my-0 py-1 text-mdlg;
}

.footerLogoBox{
  @apply flex flex-col justify-evenly items-center;
  /* @apply flex lg:flex-row; */
}

.footerTopBox{
  @apply flex flex-wrap justify-evenly items-center;
  @apply flex lg:flex-row;
  @apply mb-4 mt-3
}

.footerBottomBox{
  @apply flex flex-col justify-evenly items-center;
  @apply flex lg:flex-row;
  @apply my-2 h-[auto]
}

.footerFlagImages{
  @apply flex flex-col justify-evenly items-center;
  @apply flex lg:flex-row;
  @apply m-1
}

.footerBottomLinks{
  @apply flex flex-col justify-evenly items-center;
  @apply flex lg:flex-row;
}

.footerSocialIcons{
  @apply flex flex-col justify-evenly items-center;
  @apply flex lg:flex-row;
}
