.topNavWrapper {
    @apply flex flex-row lg:justify-between items-center py-4 top-0 z-40 max-w-full;
}

.topNavLinksWrapper {
    @apply flex flex-col justify-between items-center lg:mr-7;

    @apply xl:flex-row hidden xl:block;
}

.topNavLinks {
    @apply uppercase text-sm xl:text-lg mx-5 p-2 hover:cursor-pointer;
}
